// ==============================================================================
//           Service pour les Appels API du Tableau de Bord
//
// Rôle : Ce fichier centralise tous les appels API liés à la récupération
// des données pour les différents tableaux de bord de l'application.
//
// Il favorise un endpoint unique (`getMainDashboardData`) pour optimiser
// le chargement initial.
// ==============================================================================

import apiClient from './api';
import { API_ENDPOINTS } from '../utils/constants';

/**
 * [MÉTHODE PRINCIPALE] Récupère toutes les données agrégées pour le tableau de
 * bord principal en un seul appel API. C'est la méthode à utiliser pour le
 * chargement initial de la page.
 * @returns {Promise<object>} Un objet contenant toutes les données du dashboard
 *                            (kpis, salesChart, etc.).
 */
const getMainDashboardData = async () => {
  // Cet endpoint unique est géré par `dashboardController.js` côté backend.
  // Note: API_ENDPOINTS.DASHBOARD est juste '/dashboard'
  const response = await apiClient.get(API_ENDPOINTS.DASHBOARD);
  return response.data;
};


// --- Fonctions Individuelles (pour des rafraîchissements ciblés) ---
// Ces fonctions ne sont utiles que si une fonctionnalité nécessite de
// rafraîchir une seule partie du tableau de bord sans recharger le reste.

/**
 * Récupère uniquement les indicateurs de performance clés (KPIs) commerciaux.
 * @returns {Promise<object>} L'objet contenant les KPIs.
 */
const getKpisCommerciaux = async () => {
  const response = await apiClient.get(`${API_ENDPOINTS.DASHBOARD}/kpis/commerciaux`);
  return response.data;
};

/**
 * Récupère uniquement les données pour le graphique des ventes annuelles.
 * @returns {Promise<object>}
 */
const getSalesChartData = async () => {
  const response = await apiClient.get(`${API_ENDPOINTS.DASHBOARD}/charts/sales`); // Exemple de route
  return response.data;
};


// On exporte un objet contenant toutes les fonctions du service.
const dashboardService = {
  getMainDashboardData,
  getKpisCommerciaux,
  getSalesChartData,
};

export default dashboardService;